import React from 'react'
import illustrator from '../assets/logos/illustrator.png'
import figma from '../assets/logos/figma.png'
import postman from '../assets/logos/xd.png'
import xd from '../assets/logos/postman.png'

export default class Tools extends React.Component {
    render () {
        const tools = [illustrator, figma, postman, xd]
        return (
            <div className='px-5 flex justify-center'>
                { tools.map(tool => <img className='w-10 h-10 rounded-lg mx-2' src={tool}/> ) }
            </div>
        )
    }
}