import React from 'react'
import react from '../assets/logos/react.jpg'
import vue from '../assets/logos/vue.png'
import typescript from '../assets/logos/typescript.svg'
import javascript from '../assets/logos/javascript.svg'
import node from '../assets/logos/node.png'


export default class Skills extends React.Component {
    constructor(props) {
        super(props)
    }

    getColor(percentage) {
        if (percentage == 100) return 'purple-800'
        if (percentage >= 50) return 'blue-800'
        return 'gray-700'
    }

    getLabel(percentage) {
        if (percentage == 100) return 'avanzado'
        if (percentage >= 50) return 'intermedio'
        return 'basico'
    }

    render() {
        const skills = [
            {name: 'Javascript', logo: javascript, points: 100},
            {name: 'Typescript', logo: typescript, points: 70},
            {name: 'Vue', logo: vue, points: 100},
            {name: 'React', logo: react, points: 50},
            {name: 'Node', logo: node, points: 90}
        ].sort( (current, next) => {
            if (current.points > next.points) return 1
            if (current.points < next.points) return -1
            return 0
        } )
        return (
            <div className='px-10'>
                {skills.map((i, index) => {
                    return <div className='flex'>
                        <img className='h-10 w-10 my-3 rounded-sm mr-3' src={i.logo}/>
                        <div className='flex-grow self-center'>
                            <p className='text-left text-white capitalize font-medium text-sm'>{i.name}:  {this.getLabel(i.points)}</p>
                            <div className={`h-4 block rounded-full bg-${this.getColor(i.points)}`}
                                style={{width: `${i.points}%`}}></div>
                        </div>
                    </div>
                })}
            </div>
        )
    }
}