import React from 'react'

export default class Cloud extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {
        const styles = {
            top: Math.floor(Math.random() * (10 - 2 + 1)) + 2 + 'vh'
        }
        return (
            <div style={styles}
                className={this.props.speed, "bg-gray-800 rounded-full h-8 w-40 cloud"}></div>
        )
    }
}